@use "../global" as *;
html {
  // 1px=1remにするための設定。
  font-size: 62.5%;
  scroll-behavior: smooth;
  scroll-padding-top: $space_lg;
  @media #{$pc} {
    scroll-padding-top: $space_3xl;
  }
}
body {
  //chromeのfont-size表示バグのためbodyのみem指定。各要素はrem指定する。
  font-size: $fs_sm;
  font-family: $body_font_family;
  color: $body_text_color;
  line-height: 1;
  //SPランドスケープモード時の文字拡大防止。
  -webkit-text-size-adjust: 100%;
  //タップ時のハイライト表示を消す。
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  @media #{$pc} {
    font-size: $fs;
  }
}
a {
  color: $text_link_color;
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
